
.monitor-map-container{
    text-align: center;
}
  
/*Para react-leaflet usar <MapContainer className='mapa-basico' ... */
.mapa-basico{
    width: auto;
    height: 500px;
    box-shadow: 10px 10px 10px #888;
    left: center;
    border: 1px solid black;
    margin: 10px;
}

